import React, { useEffect, useState } from "react";
import { CCard, CCardBody, CCardHeader, CCol, CRow } from "@coreui/react";
import CIcon from "@coreui/icons-react";
import { useSelector } from "react-redux";
import axios from "axios";
import { selectUser } from "src/localyStore";

const Admin = ({ match }) => {
  const [adminData, setAdminData] = useState(null);
  const { token } = useSelector(selectUser);
  useEffect(() => {
    axios
      .get(process.env.REACT_APP_SERVER + "/api/auth/adminDetails", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((data) => {
        setAdminData(data.data.ultiModealTeam);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [token]);

  const admin = adminData?.find(
    (admin) => admin._id.toString() === match.params.id
  );
  const adminDetails = admin
    ? Object.entries(admin)
    : [
        [
          "id",
          <span>
            <CIcon className="text-muted" name="cui-icon-ban" /> Not found
          </span>,
        ],
      ];
  console.log(adminData);
  return (
    <CRow>
      <CCol lg={6}>
        <CCard>
          <CCardHeader>Admin id: {match.params.id}</CCardHeader>
          <CCardBody>
            <table className="table table-striped table-hover">
              <tbody>
                {adminDetails?.map(([key, value], index) => {
                  return (
                    <tr key={index.toString()}>
                      <td>{`${key}:`}</td>
                      <td>
                        <strong>{value}</strong>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </CCardBody>
        </CCard>
      </CCol>
    </CRow>
  );
};

export default Admin;
